<template>
  <div>
    <TemplatePlayGround
        :header-nav="headerNav"
        :template-name="sectionName"
        :template-title="sectionTitle"
        :template-description="sectionDescription">
    </TemplatePlayGround>

    <!--region Main Content-->
    <section class="blog">
      <div class="container">

        <!-- Load Dataset -->
        <div class="card-body">
          <h3>MNIST Dataset</h3>
          <div class="row form-group">
            <div class="col">
              <label>Number of Train Data: (10 - 60000)</label>
              <input v-model='numTrain' @change="numberOfDataChange" min="10" max="60000" class="form-control"
                     type="number">
            </div>
            <div class="col">
              <label>Number of Test Data: (10 - 10000)</label>
              <input v-model='numTest' @change="numberOfDataChange" min="10" max="10000" class="form-control"
                     type="number">
            </div>
          </div>
          <Button text="Load Data" :click="loadData" :disabled="dataLoaded"></Button>
          <Alert :text="loadDataStatus"></Alert>
          <h4 v-if="dataLoaded">Test Data Example</h4>
          <div v-show="dataLoaded" id="mnist-preview">
          </div>
        </div>

        <hr>

        <!-- Predefined Model -->
        <div class="form-group row">
          <label class="col-sm-3 col-form-label">Predefined Model</label>
          <div class="col-sm-9 d-flex align-items-center justify-content-center">
            <label class="radio-inline m-1 mx-4">
              <input type="radio" name="predefinedModel" value="ann" v-model="predefinedModel"
                     @change="componentKey += !componentKey"> ANN</label>
            <label class="radio-inline m-1 mx-4">
              <input type="radio" name="predefinedModel" value="cnn" v-model="predefinedModel"
                     @change="componentKey = !componentKey" checked> CNN</label>
          </div>
        </div>

        <hr>

        <!-- Model Architecture -->
        <div>
          <ModelMaker
              :input-shape="inputShape"
              :output-shape="outputShape"
              :default-learning-rate="0.01"
              :default-layers="defaultLayers"
              :default-loss="'metrics.categoricalCrossentropy'"
              :default-optimizer="'rmsprop'"
              :defaultBatchSize="100"
              :defaultEpochs="1"
              :default-backend="'webgl'"
              :dataLoaded="dataLoaded"
              :metrics="['accuracy']"
              @trainModel="trainModel"
              :key="componentKey">
          </ModelMaker>
        </div>

        <hr v-if="model">

        <!-- Graphs -->
        <Graph v-if="model" ref="graph"></Graph>

        <hr v-if="model">

        <!-- Train Model -->
        <div v-if="model" class="card-body">
          <div class="row">

            <div class="col">
              <Alert :text="trainingStatus"></Alert>
              <Alert :text="numIter"></Alert>
            </div>

            <div class="col">
              <Alert :text="train_iter"></Alert>
              <Alert :text="train_acc"></Alert>
            </div>

          </div>
        </div>

        <hr>

        <!-- Evaluate Model -->
        <div class="card-body">
          <h3>Evaluate Model</h3>
          <div class="form-group">
            <Button text="Evaluate Model" :click="evaluate"
                    :disabled="!modelTrained || (!modelLoaded && !this.data.isDownloaded)"></Button>
          </div>
          <div class="row">
            <div class="col">
              <h3>Class Accuracy</h3>
              <div class="form-group" id="class-accuracy"></div>
              <Alert :text="test_acc"></Alert>
            </div>
            <div class="col">
              <h3>Confusion Matrix</h3>
              <div class="form-group" id="confusion-matrix"></div>
            </div>
          </div>
          <hr>
          <h3>Prediction Example</h3>
          <Button text="Show Prediction" :click="showExample"
                  :disabled="!modelTrained || (!modelLoaded && !this.data.isDownloaded)"></Button>
          <div id="example-preview"></div>
        </div>

        <hr>

        <!-- Save/Load Model -->
        <div class="card-body">
          <div class="row">
            <div class="col border-right">
              <h3>Save Model</h3>
              <Button text="Save Model" :click="saveModel" :disabled="!modelTrained"></Button>
              <Alert text="Model Saved" :condition="modelSaved"></Alert>
            </div>
            <div class="col">
              <h3>Load Model</h3>

              <div class="ds-parent-btn">
                <label class="btn ds-btn btn-file">Browse JSON
                  <input @change="onFileSelect($event, 'json')" type="file" id="json-upload">
                </label>
                <label v-if="jsonFileName">{{ jsonFileName }}</label>
              </div>

              <div class="ds-parent-btn">
                <label class="btn ds-btn btn-file">Browse Weights
                  <input @change="onFileSelect($event, 'weights')" type="file" id="weights-upload">
                </label>
                <label v-if="weightsFileName">{{ weightsFileName }}</label>
              </div>

              <Button text="Load Model" :click="loadModel" :disabled="!jsonLoaded || !weightsLoaded"></Button>
              <Alert text="Model Loaded" :condition="modelLoaded"></Alert>
            </div>
          </div>
        </div>

        <hr>

        <!-- Test Model -->
        <div class="card-body">
          <h3>Draw New Image</h3>
          <div class="row form-group">
            <div class="col text-center d-flex align-items-center justify-content-center">
              <canvas id="predict-canvas" width="140" height="140" style="border:1px solid #000000;"></canvas>
            </div>
            <div class="col" style="text-align:center">
              <div class="form-group">
                preview <br>
                <canvas id="preview-canvas" width="28" height="28" style="border:1px solid #000000;"></canvas>
              </div>
              <Alert :text="prediction"></Alert>
            </div>
            <div class="col">
              Prediction Class Histogram
              <div id="predict-graph"></div>
            </div>
          </div>
          <div class="row">
            <Button class="col" text="Clear" :click="clear"></Button>
            <Button class="col" text="Predict" :click="predict" :disabled="!(modelInitialized || modelLoaded)"></Button>
          </div>
        </div>

      </div>
    </section>
    <!--endregion-->
  </div>
</template>

<script>
import TemplatePlayGround from "@/components/TemplatePlayGround";
import ModelMaker from "@/components/ModelMaker";
import Graph from "@/components/Graph";
import Button from "@/components/Button";
import Alert from "@/components/Alert";
import * as tf from '@tensorflow/tfjs'
import * as tfvis from '@tensorflow/tfjs-vis'
import {MnistData} from './mnist_data'
import * as util from './mnist_utils'
import {initCanvas} from './draw_utils'

require('babel-polyfill')
export default {
  components: {
    TemplatePlayGround,
    ModelMaker,
    Graph,
    Button,
    Alert
  },
  data() {
    return {
      headerNav: 'PlayGround',
      sectionName: 'MNIST Classification',
      sectionTitle: 'MNIST Classification using TensorFlow.js',
      sectionDescription: `MNIST handwritten digit database is a popular multi-class classification dataset
      that is widely used in Machine Learning tutorials to understand the basic of classification.
      The original MNIST database consist of 60,000 training images and 10,000 testing images.
      It is a dataset of small square 2828 pixel grayscale images of handwritten single digits between 0 and 9.
      The task is to classify a given image of a handwritten digit into one of 10 classes representing integer
      values from 0 to 9, inclusively.`,

      componentKey: true,

      numTrain: 6000,
      numTest: 1000,
      loadDataStatus: null,
      dataLoaded: false,

      inputShape: [28, 28, 1],
      outputShape: 10,
      predefinedModel: 'cnn',
      model: null,
      modelInitialized: false,
      modelTrained: false,
      trainingStatus: null,
      numIter: null,
      train_iter: null,
      train_acc: null,
      test_acc: null,

      modelSaved: false,
      modelLoaded: false,
      jsonLoaded: false,
      jsonFileName: null,
      weightsLoaded: false,
      weightsFileName: null,

      prediction: null
    }
  },
  methods: {
    round(num) {
      return parseFloat(num * 100).toFixed(1)
    },
    prepare() {
      initCanvas('predict-canvas')
    },
    onFileSelect(event, fileName) {
      if (fileName === 'json') {
        this.jsonFileName = event.target.files[0].name
        this.jsonLoaded = true
      } else if (fileName === 'weights') {
        this.weightsFileName = event.target.files[0].name
        this.weightsLoaded = true
      }
    },
    numberOfDataChange() {
      this.dataLoaded = false
      if (parseInt(this.numTrain) + parseInt(this.numTest) > 70000) {
        this.numTrain = 60000
        this.numTest = 10000
      }
      if (parseInt(this.numTrain) + parseInt(this.numTest) < 20) {
        this.numTrain = 10
        this.numTest = 10
      }
    },
    async loadData() {
      this.loadDataStatus = 'Downloading MNIST data. Please wait...'
      await this.data.load(parseInt(this.numTrain), parseInt(this.numTest))
      this.dataLoaded = true
      this.loadDataStatus = 'MNIST data Loaded'
      const [xTest, yTest] = this.data.getTestData(8)
      const labels = Array.from(yTest.argMax(1).dataSync())
      util.showExample('mnist-preview', xTest, labels)
    },
    async trainModel(model, batchSize, epochs, modelInitialized) {
      this.statusMessage = null
      try {
        this.model = model
        this.modelInitialized = modelInitialized
        this.trainingStatus = 'Training, please wait...'

        const [xTrain, yTrain] = this.data.getTrainData()
        let nIter = 0
        const numIter = Math.ceil(xTrain.shape[0] / batchSize) * epochs
        this.numIter = 'Num Training Iteration: ' + numIter

        await this.model.fit(xTrain, yTrain, {
          epochs: epochs,
          batchSize: batchSize,
          shuffle: true,
          callbacks: {
            onBatchEnd: async (batch, logs) => {
              nIter++
              await this.$refs.graph.showGraph(logs)
              this.train_iter = `Training... (${this.round(nIter / numIter)}%)`
              this.train_acc = 'Train Accuracy: ' + this.round(logs.acc) + '%'
            }
          }
        })
        this.trainingStatus = 'Training Done.'
        this.modelTrained = true
      } catch (err) {
        this.model = null
        this.modelInitialized = false
        this.modelTrained = false
        this.statusMessage = err.message;
        console.log(err)
      }
    },
    async evaluate() {
      const classNames = ['Zero', 'One', 'Two', 'Three', 'Four', 'Five', 'Six', 'Seven', 'Eight', 'Nine']
      const [xTest, yTest] = this.data.getTestData()
      const yPred = await this.model.predict(xTest).argMax(1)
      const yLabel = yTest.argMax(1)
      const evalTest = await tfvis.metrics.accuracy(yLabel, yPred)
      this.test_acc = 'Test Accuracy: ' + this.round(evalTest) + '%'
      const classAcc = await tfvis.metrics.perClassAccuracy(yLabel, yPred)
      const confMt = await tfvis.metrics.confusionMatrix(yLabel, yPred)
      const conf = document.getElementById('confusion-matrix')
      const acc = document.getElementById('class-accuracy')
      await tfvis.show.perClassAccuracy(acc, classAcc, classNames)
      await tfvis.render.confusionMatrix(conf, {values: confMt, tickLabels: classNames})
    },
    showExample() {
      const [xTest, yTest] = this.data.getTestData(16)
      const yPred = this.model.predict(xTest)
      const labels = Array.from(yTest.argMax(1).dataSync())
      const predictions = Array.from(yPred.argMax(1).dataSync())
      util.showExample('example-preview', xTest, labels, predictions)
    },
    async saveModel() {
      await this.model.save('downloads://')
      // util.firefoxSave(this.model)
      this.modelSaved = true
    },
    async loadModel() {
      const jsonUpload = document.getElementById('json-upload')
      const weightsUpload = document.getElementById('weights-upload')
      this.model = await tf.loadLayersModel(tf.io.browserFiles([jsonUpload.files[0], weightsUpload.files[0]]))
      this.modelLoaded = true
    },
    clear() {
      const context = this.canvas.getContext('2d')
      context.clearRect(0, 0, this.canvas.width, this.canvas.height)
    },
    async predict() {
      const preview = document.getElementById('preview-canvas')

      const img = tf.browser.fromPixels(this.canvas, 4)
      const resized = util.cropImage(img, this.canvas.width)

      await tf.browser.toPixels(resized, preview)
      const xData = tf.cast(resized.reshape([1, 28, 28, 1]), 'float32')

      const yPred = this.model.predict(xData)

      const prediction = Array.from(yPred.argMax(1).dataSync())
      this.prediction = 'Predicted: ' + prediction

      const barchartData = Array.from(yPred.dataSync()).map((d, i) => {
        return {index: i, value: d}
      })
      await tfvis.render.barchart(document.getElementById('predict-graph'), barchartData, {width: 400, height: 140})
    }
  },
  computed: {
    data() {
      return new MnistData()
    },
    defaultLayers() {
      if (this.predefinedModel === 'ann') {
        return [
          {type: 'flatten'},
          {type: 'dense', units: 42, activation: 'relu'},
          {type: 'dense', units: 10, activation: 'softmax'}]
      } else if (this.predefinedModel === 'cnn') {
        return [
          {type: 'conv2d', kernelSize: 3, filters: 16, activation: 'relu'},
          {type: 'conv2d', kernelSize: 3, filters: 16, activation: 'relu'},
          {type: 'maxPooling2d', poolSize: 2, strides: 2},
          {type: 'flatten'},
          {type: 'dense', units: 10, activation: 'softmax'}]
      } else return null
    },
    canvas() {
      return document.getElementById('predict-canvas')
    }
  },
  mounted() {
    this.prepare()
  }
}
</script>

<style scoped>

.btn-file input[type=file] {
  display: none;
}

.ds-parent-btn {
  text-align: center;
}

.ds-btn {
  background: #fff;
  font-family: "Nunito", sans-serif;
  font-weight: 600;
  font-size: 14px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 12px 32px;
  border-radius: 50px;
  transition: 0.3s;
  line-height: 1;
  color: #e43c5c;
  -webkit-animation-delay: 0.8s;
  animation-delay: 0.8s;
  margin: 6px;
  border: 2px solid #e43c5c;
}

.ds-btn:hover {
  background: #e43c5c;
  color: #fff;
  text-decoration: none;
}

.ds-btn:focus {
  outline: none;
}

.ds-btn:disabled {
  background-color: #fdeff2;
  color: #e43c5c;
  opacity: 0.65;
  cursor: not-allowed;
}

</style>